import '../css/index.css';

let id = sessionStorage.getItem("id");
let api = 'ce2eb2231a371296cf6ff11a39206d6e';
	fetch(
		`https://api.themoviedb.org/3/movie/${id}?api_key=${api}&language=en-US&append_to_response=reviews,videos,images`
	)
	.then(res => res.json())
	.then(data => {
		console.log(data);
		let output = '';
		output = `
			<div id="movie">
				<div class="movie-infos">
					<h1>${data.title}</h1>
					<p>${data.genres[0].name + ', ' + data.genres[1].name}</p>
				</div>
			</div>
		`
		document.body.innerHTML = output;
		document.getElementById('movie').setAttribute("style", `background: linear-gradient(0deg, rgb(0, 0, 0) 5%, rgba(0, 0, 0, 0.45) 92%) no-repeat center/cover, url(https://image.tmdb.org/t/p/original${data.backdrop_path}) no-repeat center top/cover rgb(255, 255, 255)`);
	})